<template>
    <header>
        <a href="/" class="logo">B<span>O</span>M<span>IN</span></a>
        <nav>
            <ul>
                <li v-for="nav in navs" :key="nav.id">
                    <a href="#">{{nav.display}}</a>
                    <span class="nav-line"></span>
                </li>
            </ul>
        </nav>
    </header>
</template>

<script>
    export default {
        data(){
            return{
                navs:[
                    {display:"JS",name:"JS"},
                    {display:"NODE JS",name:"NODE JS"},
                    {display:"VUE JS",name:"VUE JS"},
                    {display:"OAUTH2",name:"OAUTH2"}
                    ]
            }
        }
    }
</script>

<style lang="scss" scoped>
    $header-height: 70px;
    $site-color: #34445D;
    header{
        height: $header-height;
        background-color: white;
        line-height: $header-height;
        padding: 0px 0px 0px 20px;
        position: fixed;
        top: 0;
        width: 100%;
        box-sizing: border-box;
        .logo{
            font-size: 30px;
            font-weight: bold;
            color: $site-color;
            display: inline-block;
            span{
                font-weight: 300;
            }
        }
        nav{
            display: block;
            float: right;
            li{
                display: inline-block;
                min-width: 120px;
                font-size: 20px;
                text-align: center;
                height: $header-height;
                position: relative;
                cursor: pointer;
                a{
                    color: #222222;
                }
                .nav-line{
                    width: 0px;
                    left: 0;
                    bottom: 0;
                    border-bottom: 3px solid $site-color;
                    display: inline-block;
                    position: absolute;
                    -webkit-transition: width 1s; /* Safari prior 6.1 */
                    transition: width 1s;
                }
                &:hover{
                    background-color: #fefefe;
                    .nav-line{
                        width: 100%;
                    }
                    a{
                        font-weight: bold;
                        color: $site-color;
                    }
                }
            }
        }
    }
</style>


