<template>
    <div class="container">
        <h1>
            <span class="title">This is</span>
            <span class="title">Bomin's</span>
            <span class="title">Web Site</span>
        </h1>
    </div>
</template>

<script>
    export default {
        methods:{
           
        },
        mounted(){
          
        }
    }
</script>

<style lang="scss" scoped>
    $red: #e55643 ;
    $green: #2b9f5e;
    $yellow: #f1c83c;
    $shadow: #533d4a;
    .container{
        top: 70px;
        background-image: url('./../assets/img/background.jpg');
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        position: fixed;
        left: 0;
        margin-bottom: 20px;
        width: 100%;
        height: 100%;

        h1{
            color: #fff;
            text-transform: uppercase;
            font-size: 93px;
            margin: 0;
            line-height: 120px;
            letter-spacing: 2px;
            margin-top: 8%;
        }

        .title{
            transform: translateX(-50%) rotate(-10deg);
            left: 50%;
            position: relative;
            display: block;
            text-align: center;
            text-shadow: $shadow 1px 1px, $shadow 2px 2px, $shadow 3px 3px, $shadow 4px 4px, $shadow 5px 5px, $shadow 6px 6px;
        }

        .title{
            -webkit-animation: bounce 5s infinite;
            -moz-animation: bounce 5s infinite;
            -o-animation: bounce 5s infinite;
            animation: bounce 5s infinite;
            &:nth-child(1){
                color: $red;
            }
            &:nth-child(2){
                color: $green;
            }
            &:nth-child(3){
                color: $yellow;
            }
        }

        @-webkit-keyframes bounce {
            0%       { bottom:30px; }
            50%      { bottom:-20px; }
            100%     {bottom: 40px;}
        }

        @-moz-keyframes bounce {
            0%       { bottom:40px; }
            50%      { bottom:-10px; }
            100%     {bottom: 50px;}
        }

        @-o-keyframes bounce {
            0%       { bottom:40px; }
            50%      { bottom:-10px; }
            100%     {bottom: 50px;}
        }

        @keyframes bounce {
            0%       { bottom:40px; }
            50%      { bottom:-10px; }
            100%     {bottom: 50px;}
        }

        @media only screen and (max-width: 600px)  {
             h1{
                font-size: 70px;
                line-height: 100px;
            }
        }

        @media only screen and (max-width: 400px)  {
             h1{
                font-size: 50px;
                line-height: 70px;
            }
        }
    }
</style>


